export const colors = {
    red: '\x1b[31m',
    green: '\x1b[32m',
    yellow: '\x1b[33m',
    darkGrey: '\x1b[90m',
    blue: '\x1b[34m',
    reset: '\x1b[0m',
    bold: '\x1b[1m',
    underline: '\x1b[4m',
    u: (text: string) => `${colors.underline}${text}${colors.reset}`,
    b: (text: string) => `${colors.bold}${text}${colors.reset}`,
  };